.superInput {
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,0.3);
    box-shadow: 0 0 10px rgba(0,0,0,0.3);
    margin-bottom: 22px;
}

.errorInput {
    border: 1px solid black;
    background: rgba(255,0,0,0.3);
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

.error {
    color: red;
    font-weight: bold;
}


