.default, .red {
    padding: 0 20px;
    margin: 5px 0;
    border: 1px solid #fff;
    border-radius: 50px;
    height: 30px;
    font-size: 14px;
    font-weight: bold;
    box-shadow: 0 4px 6px rgb(65 132 144 / 10%), 0 1px 3px rgb(0 0 0 / 8%);
    cursor: pointer;
}

.default {
    background-color: #fff;
}

.red {
    background-color: red;
}

.default:hover, .red:hover {
    transition: all 0.1s;
    box-shadow: 0 7px 14px rgb(65 132 144 / 10%), 0 3px 6px rgb(0 0 0 / 8%);
}

.default:hover {
    background-color: #eee;
}

.red:hover {
    border: 3px solid black;
}

.default:active, .red:active {
    background-color: #808080;
}
.default:disabled {
    background-color: #eee;
    border-color: #eee;
    color: #444;
    cursor: not-allowed;
}